<template>
<div>
  <div class="row py-2">
          <div class="col-12 col-md-2">
            <h4 class="ms-4">Businesses</h4>
          </div>
          <div class="col-12 col-md-7 py-1">
            <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details>
            </v-text-field>
          </div>
          <div class="col-12 col-md-3 py-1">
            <router-link to="/reseller/client/new">
              <v-btn tile color="error" large>
                <v-icon left>
                  mdi-account-plus
                </v-icon>
                ADD BUSINESS CLIENT
              </v-btn>
            </router-link>
          </div>
        </div>
  <v-tabs v-model="tab">
    <v-tab cols="6">Business List</v-tab>
  </v-tabs>
  <v-tabs-items v-model="tab">
          <v-tab-item>
            <v-card color="basil" flat >
              <v-form>
                <v-row>
                  <v-col class="d-flex" cols="12" md="4">
                    <v-autocomplete :items="cities" item-text="city" item-value="id" label="City" v-model="city" @change="getSelectedData()" dense></v-autocomplete>
                  </v-col>
                  <v-col class="d-flex" cols="12" md="4">
                    <v-autocomplete :items="states" item-text="state" item-value="id" v-model="state" label="State" @change="getSelectedData()" dense></v-autocomplete>
                  </v-col>
                  <v-col class="d-flex" cols="12" md="4">
                    <v-autocomplete :items="Status" item-text="name" item-value="id" v-model="statusValue" label="Active/De-active" @change="getSelectedData()" dense></v-autocomplete>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col class="d-flex" cols="12" md="4">
                    <v-autocomplete :items="plans" item-text="plan_description" v-model="plan" item-value="id" label="Plan" @change="getSelectedData()" dense></v-autocomplete>
                  </v-col>

                  <v-col class="d-flex" cols="12" md="4">
                    <v-autocomplete :items="subcategories" item-text="subcat_name" v-model="subcategory" item-value="id" @change="getSelectedData()" label="Sub Category" dense></v-autocomplete>
                  </v-col>

                  <v-col cols="12" md="4">
                    <v-menu v-model="menu2" :close-on-content-click="false" :nudge-right="40"
                            transition="scale-transition" offset-y min-width="auto">
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field dense v-model="date" label="Expire (with in week)" prepend-icon="mdi-calendar"
                                      v-bind="attrs" v-on="on"></v-text-field>
                      </template>
                      <v-date-picker v-model="date" @change="getSelectedData()" @input="menu2 = false"  ></v-date-picker>
                    </v-menu>
                  </v-col>
                </v-row>
                <div class="">
                  <v-data-table :headers="headers" :search="search" :items="businesslists" sort-by="calories" :single-select="singleSelect"
                                hide-header
                                show-select class="elevation-1">
                    <template v-slot:top>
                      <v-divider class="mx-4" inset vertical></v-divider>
                      <v-spacer></v-spacer>
                      <v-dialog v-model="dialogDelete" max-width="500px">
                        <v-card>
                          <v-card-title class="text-h5">Are you sure you want to delete this
                            item?</v-card-title>
                          <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                            <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                            <v-spacer></v-spacer>
                          </v-card-actions>
                        </v-card>
                      </v-dialog>
                    </template>
                      <template v-slot:[`item.action`]="{item}">
                          <router-link :to="{name: 'updatebusinesslist',params: { business_id: item.id }}">
                            <v-icon small class="mr-2" title="Edit Business">
                              mdi-pencil
                            </v-icon>
                          </router-link>
                        <v-icon small class="mr-2" title="Login As Business" @click="resellerbusinessLogin(item.email)"> mdi-login </v-icon>
                      </template>
                    <template v-slot:[`item.active`]>
                      <v-icon small class="mr-2">
                        mdi-check
                      </v-icon>
                    </template>
                  </v-data-table>
                  <div class="text-muted my-2">
                    <v-btn gray large>DELETE SELECTED</v-btn>
                  </div>
                </div>
              </v-form>
            </v-card>
          </v-tab-item>
        </v-tabs-items>
</div>
</template>
<script>
import axios from "axios";

export default {
  name: 'BusinessList',
  components: {},
  data: () => ({
    tab: '',
    search: '',
    cities: [],
    states: [],
    plans: [],
    subcategories: [],
    Status: [
      {'id':1,'name':'Active'},
      {'id':0,'name':'Inactive'}
    ],
    menu: false,
    menu2: false,
    modal: false,
    dialog: false,
    dialogDelete: false,
    headers: [
      {text: "Name", align: "start", sortable: false, value: "name"},
      { text: "E-mail", value: "email" },
      { text: "Mobile Number", value: "mobile_phone" },
      { text: "Plan", value: "plan_description" },
      { text: "Reseller", value: "reseller_name" },
      { text: "Expires", value: "expires" },
      { text: "City", value: "city" },
      { text: "Sub Category", value: "subcatsname" },
      { text: "Listing Date", value: "created_date" },
      { text: "Active", value: "active", sortable: false },
      { text: "Actions", value: "action", sortable: false },
    ],
    businesslists: [],
    searchparams: [],
  }),
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.getBusiness();
    this.getSearchData();
  },

  methods: {

    getBusiness(){
      axios.get('registered/business/list')
          .then((resp)=>{
            this.businesslists = resp.data.businesses;
          });
    },
    async resellerbusinessLogin(email) {
      var data = {
        email: email,
      };
      
      // Make API call to authenticate the user and obtain token and user data
      await axios.post('reseller/business/login', data)
          .then((resp) => {
            const userId = resp.data.user;
            this.$store.dispatch('setuserid', { userId });
            this.$router.push({name: 'businessdashboard'});
          })
          .catch((err)=>{
            this.error = err.response.data.message;
          })
    },
    // To show the search information data in dropdowns (code start)
    getSearchData(){
      axios.get('get/information')
          .then((resp)=>{
            this.cities = resp.data.cities;
            this.states = resp.data.state;
            this.plans = resp.data.plan;
            this.subcategories = resp.data.subcategories;
          });
    },
    // To show the search information data in dropdowns (code end)

    // To show get the data according to filter options ( code start )
    getSelectedData(){
      
      var params = {
        city : this.city,
        state : this.state,
        statusValue : this.statusValue,
        plan : this.plan,
        subcategory : this.subcategory,
        date : this.date,
      };
      axios.post('search/business', params )
          .then((resp)=>{
            console.log(resp);
            this.businesslists = resp.data.businesses;
          });
    },
    // To show get the data according to filter options ( code end )

    editItem(item) {
      this.editedIndex = this.businesslists.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    deleteItem(item) {
      this.editedIndex = this.businesslists.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },
    deleteItemConfirm() {
      this.businesslists.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
  },
}
</script>